.dropdown-content {
    border-radius: 0 0 50px 50px;
    width: 100%;
    margin-bottom: 20px;

    @include desktop {
        margin-bottom: 30px;
    }
}

.dropdown-list {
    display: block;
    width: 100%;
    margin-top: -5px;
    padding: 22px 11px 19px 11px;
    border-radius: 0 0 5px 5px;
    background-color: $primary-background-grey-light;
    color: $primary-color;
    font-weight: 400;

    @include desktop {
        padding: 33px 18px 19px 18px;
    }
}