.about-content {
  @include externalPaddingMobile;

  @include desktop {
    @include externalPaddingDesktop;
  }
}

.about-content__bandeau {
  overflow: hidden;
  height: 223px;
  border-radius: 25px;
}

.about-dropdown {
  max-width: 1023px;
  margin: 19px auto 0 auto;
  height: auto;

  & li,
  p {
    font-size: 12px;
  }
  
  & button {
    height:30px;
    font-size: 13px;
  }

  @include desktop {
   margin: 31px auto 0 auto;

    & li,
    p,
    button {
      font-size: 24px;
    }
    & button {
      height: 47px;
    }
  }

}