.content-slideShow {
    position: relative;
    width: 100%;
    height: 100%;

}

.slideShow-img {
    width: 100%;

    object-position: 100% 100%;
    object-fit: cover;
}

.slideShow-arrow {
    display: block;
    position: absolute;
    top: 36%;
    width: 30px;
    height: 30px;
    background-color: green;
    background-size: cover;
    transition: transform .4s;

    &:hover {
        transform: scale(1.2);
    }

    @include desktop {
        width: 80px;
        height: 80px;
    }
}

.left {
    left: 20px;
    background-image: url("../../assets/arrow-left.png");
}

.right {
    right: 20px;
    background-image: url("../../assets/arrow-right.png");
}