.bandeau-content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
    position: relative;

    @include desktop {
        justify-content: center;
    }
}

.bandeau-content__img {
    height: 100%;
    width: 100%;
    object-position: 100% 100%;
    object-fit: cover;
  /*   -webkit-filter: brightness(70%);
    filter: brightness(70%); */

    @include mobile-landscape {
        object-position: 50% 50%;
    }
}
.filter{
    -webkit-filter: brightness(70%);
    filter: brightness(70%);
}

.bandeau-content__text {
    display: block;
    position: absolute;
    width: 80%;
    padding-left: 20px;
    color: $color-white;

    @include tablette-only {
        width: auto;
    }
}