/*-- Media query-- */

@mixin mobile-landscape{
  @media screen and(min-width: map-get($breakpoints, mobile-landscape)) {
    @content;
  }
}

@mixin tablette-only {
  @media screen and(min-width: map-get($breakpoints, tablette)) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: map-get($breakpoints , desktop)) {
    @content;
  }
}

@mixin tv {
  @media screen and (min-width: map-get($breakpoints , tv)) {
    @content;
  }
}

@mixin externalPaddingDesktop {
  padding-left:100px;
  padding-right: 100px;
}
@mixin externalPaddingMobile{
  padding-left:20px;
  padding-right: 20px;
}
