.descriptif-content {
  @include externalPaddingMobile;

  @include desktop {
    @include externalPaddingDesktop;
  }
}

.descriptif-content__banner {
  overflow: hidden;
  height: 255px;
  border-radius: 25px;

  @include desktop {
    height: 415px;
  }
}

.description-content__center {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  margin-top: 19px;

  @include desktop {
    margin-top: 31px;
    flex-direction: row;
  }
}

.content-center__information-location {
  width: 100%;
  color: $primary-color;

  & p {
    font-weight: 500;
    padding-bottom: 5px;
  }

  & p:nth-child(1) {
    font-size: 18px;
  }

  & p:nth-child(2) {
    font-size: 14px;
  }

  @include desktop {
    width: 70%;

    & p {
      padding-bottom: 10px;
    }

    & p:nth-child(1) {
      font-size: 36px;
    }

    & p:nth-child(2) {
      font-size: 18px;
      padding-bottom: 20px;
    }
  }
}

.information-location__tags {}


.content-center__information-user {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include desktop {
    width: 30%;
    flex-direction: column-reverse;
    align-items: flex-end;
  }
}

.information-user__card {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: auto;

  & p {
    width: 80px;
    font-size: 12px;
    font-weight: 500;
    color: $primary-color;
  }

  & img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-left: 10px;
  }

  @include desktop {

    & p {
      font-size: 18px;
    }

    & img {
      width: 64px;
      height: 64px;
    }
  }
}

.description-dropDown {
  width: 100%;
  height: auto;
  margin-top: 16px;

  & li,
  p {
    font-size: 12px;
  }

  & button {
    height: 30px;
    font-size: 13px;
  }

  @include desktop {
    margin-top: 24px;

    & li,
    p,
    button {
      font-size: 18px;
    }

    & button {
      height: 52px;
    }

    display: grid;
    grid-template-columns: 47% 47%;
    justify-content:space-between;
  }
}