.header-container {
    width: 100%;
    height: auto;
}

.header-content {
    display: grid;
    grid-template-columns: (auto auto 50%);
    align-items: center;
    background-color: $primary-background-white;
    @include externalPaddingMobile;
    padding-top: 20px;
    padding-bottom: 8px;
    @include desktop {
        @include externalPaddingDesktop;
        padding-top: 40px;
        padding-bottom: 40px;
    }
}
.header-content__logo{
    grid-column: 1;
}
.header-content__logo-img {  
    width: 145px;
    height: 47px;
    @include desktop {
        width: 210px;
        height: 68px;
    }
}
.header-content__nav{
    grid-column: 3;
    & ul{
        display:flex;
        justify-content:right;
    }
}

.header-content__nav-link { 
    height: 34px;
    margin-left: 10px;
    font-size: 12px;
    font-weight: 500;
    font-family: $font-texte;
    color: $primary-color;

    @include mobile-landscape {
        font-size: 20px;
    }

    @include desktop {
        margin-left: 57px;
        font-size: 24px;
    }
}

.active {
        text-decoration: underline;  
}